<template>
  <div>
    <el-row :gutter="20">
      <el-col class="mb20" :xl="6" :lg="6" :md="10" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>标签编辑</span>
          </div>
          <div>
            <el-form
              :model="editParams"
              label-position="top"
              :rules="tagRules"
              ref="tagForm"
              label-width="100px"
              class="vertical-form"
            >
              <el-form-item label="名称">
                <el-input v-model="editParams.name" prop="name" placeholder="名称"></el-input>
              </el-form-item>
              <el-form-item label="别名">
                <el-input v-model="editParams.alias" placeholder="别名"></el-input>
              </el-form-item>
              <el-form-item label="路径">
                <el-input v-model="editParams.path" placeholder="访问路径"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" size="medium" @click="save()">保存</el-button>
                <el-button size="medium" @click="reset()">重置</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-card>
      </el-col>
      <el-col class="mb20" :xl="18" :lg="18" :md="14" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>标签列表</span>
          </div>
          <div>
            <el-form :inline="true">
              <el-form-item label="标签名称">
                <el-input v-model="queryParams.name" placeholder="标签名称"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="getTags()">查询</el-button>
              </el-form-item>
            </el-form>
          </div>
          <div style="padding:20px;">
            <el-table :data="tableData" style="margin-bottom:20px;">
              <el-table-column prop="id" label="ID"></el-table-column>
              <el-table-column prop="name" label="标签名称"></el-table-column>
              <el-table-column prop="path" label="访问路径"></el-table-column>
              <el-table-column prop="alias" label="别名"></el-table-column>
              <el-table-column prop="createTime" label="创建时间"></el-table-column>
              <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                  <el-button type="text" @click="edit(scope.row)">编辑</el-button>
                  <el-button type="text" @click="del(scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              :background="false"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="queryParams.pageNum"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="queryParams.pageSize"
              :layout="optionsArr.join(',')"
              :total="total"
            ></el-pagination>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import rules from "@/config/rules";
import tagApi from "@/api/tagApi";
export default {
  data() {
    return {
      optionsArr: ["total", "sizes", "prev", "pager", "next", "jumper"],
      tableData: [],
      editParams: {},
      tagRules: rules.tagRules,
      queryParams: {
        pageNum: 1,
        pageSize: 10
      },
      total: 0
    };
  },
  created() {
    this.getTags();
  },
  computed: {
    editTypeChange() {
      return this.editType;
    }
  },
  watch: {},
  methods: {
    getTags() {
      if (this.queryParams.name == "") {
        this.queryParams.name = null;
      }
      tagApi.page(this.queryParams).then(res => {
        this.tableData = res.data.records;
        this.total = res.data.total;
      });
    },
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getTags();
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getTags();
    },
    save(formName) {
      if (!this.editParams.name) {
        this.$message.warning("请填写名称");
        return false;
      }
      let id = this.editParams.id;
      if (id) {
        tagApi.update(this.editParams).then(res => {
          this.$message.success("保存成功");
          this.reset();
        });
      } else {
        tagApi.create(this.editParams).then(res => {
          this.reset();
          this.$message.success("保存成功");
          this.getTags();
        });
      }
    },
    edit(row) {
      this.editParams = row;
    },
    del(row) {
      tagApi.delete(row.id).then(res => {
        this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$message.success("已删除");
            this.getTags();
          })
          .catch(() => {});
      });
    },
    reset(formName) {
      this.editParams = {};
    }
  }
};
</script>

